import NavBar from "./NavBar";
import Footer from "./Footer";
import { useState } from "react";

const BookPage = () => {

    const [showBorderOpis, setShowBorderOpis] = useState('border-yes');
    const [showBorderOpinie, setShowBorderOpinie] = useState('border-none');

    const clickOpis = () => {
        if(showBorderOpis === 'border-none'){
            setShowBorderOpis('border-yes');
            setShowBorderOpinie('border-none')

        }
    }

    const clickOpinie = () => {
        if(showBorderOpinie === 'border-none'){
            setShowBorderOpis('border-none');
            setShowBorderOpinie('border-yes')

        }
    }

    return(
        <>
        <NavBar />
        <div className="book-page">
            <div className="book-details">
                
                <div className="book-details-pro"></div>
                <div className="book-details-img"></div>
                <div className="book-details-text">
                    <div className="books">
                    <h2>Tytu</h2>
                    <div className="book-author"><span>Autor:</span></div>
                    <div className="book-year"><span>Rok wydania:</span></div>
                    <div className="book-category"><span>Kategoria:</span></div>
                    </div>
                    <div className="book-reply">
                    <span className="txt">Maria Paszyska</span>
                    <span className="txt">2002</span>
                    <span className="txt">Romantyczne</span>
                    </div>
                
                </div>
                
                <div className="book-price">
                        <span>23,45z</span>
                        <button>Do koszyka</button>
                </div>
            
            </div>
            <div className="book-opinie">

                    <div className="opinie-choose">
                    <ul>
                        <li className={showBorderOpis} onClick={clickOpis}>Opis</li>
                        <li className={showBorderOpinie} onClick={clickOpinie}>Opinie</li>
                    </ul>
                    </div>

                    {showBorderOpis === "border-yes" ? <p>Warszawa, wiosna 1938 roku. Osiemnastoletnia Zuzanna wierzy, e jej ycie bdzie usane rami. Pewnego dnia zachwyca j wygrywana na skrzypcach melodia, a kilka miesicy pniej poznaje zdolnego muzyka i traci dla niego gow. Rodzi si pierwsza mio, pojawiaj marzenia i plany, a potem przychodzi wojna. Znaczy kolejne miesice pasmem strat, cierpienia i blu

Zuzanna zaczyna prac na Poczcie Gwnej i wcza si w podziemn walk z okupantem. Zostaje przydzielona do komrki P zajmujcej si przechwytywaniem anonimowych donosw pisanych przez Polakw do Szanownego Pana Gestapo. Jeden z listw rozdziera jej serce. Okazuje si, e dziaalno konspiracyjna to niebezpieczna gra, w ktrej stawk jest nie tylko ycie, ale i mio.

Listy do Gestapo to poruszajca do gbi spowied kobiety, ktra stana przed dramatycznymi wyborami. Opowie o yciu brutalnie zmienionym przez wojn. O ranach, ktre nigdy si nie goj, winach, ktre domagaj si odkupienia, i przebaczeniu, ktre by moe nie nadejdzie.</p> : <p>Opinie</p>}
            </div>
        </div>
        <Footer/>
        </>
    )
}

export default BookPage